.hero {
	height: calc(100vh - 200px);
	background-color: $accent-color;

	&__container {
		height: 100%; }

	&__wrap {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: space-around; }

	&__card {
		display: flex;
		flex-direction: column;
		max-width: 735px;
		padding: 30px;
		gap: 25px;
		color: $white; }

	&__title {
		margin: 0;
		font-family: "Kazimir", sans-serif;
		@include adaptive-font (52, 22);
		color: $broom; }

	&__subtitle {
		margin: 0;
		@include adaptive-font (25, 18); }

	&__link {
		display: inline-block;
		max-width: 200px;
		padding: 15px 20px;
		text-align: center;
		color: $accent-color;
		background-color: $broom;
		border: 2px $accent-color solid;
		transition: all .2s;
		border-radius: 15px;
		font-size: 28px;

		&:hover {

			background-color: $accent-color;
			border: 2px $broom solid;
			color: $white; } }

	&__img {

		img {
			width: 450px;
			height: auto; } } }
